import React, {useEffect, useRef} from 'react';
import {Link} from "react-router-dom";
import useTranslation from "../../../../hooks/useTranslation";

const HeaderMenu = ({isVisible, setIsVisible}) => {

    const node = useRef();
    const handleOutsideTitleBoxClick = e => {
        if (node && !node.current.contains(e.target)) {
            setIsVisible(false);
        }
    };

    useEffect(() => {
        document.addEventListener("mousedown", handleOutsideTitleBoxClick);

        return () => {
            document.removeEventListener("mousedown", handleOutsideTitleBoxClick);
        };
    }, []);

    return (
        <nav
            ref={node}
            className={`nav ${isVisible ? 'visible': ''}`}
        >
            <a href="https://acpt.io/documentation" target="_blank">
                {useTranslation("Documentation")}
            </a>
            <a href="https://acpt.io/acpt-dashboard/" target="_blank">
                {useTranslation("My account")}
            </a>
            <Link to="/license">
                {useTranslation("My license")}
            </Link>
            <a href="https://acpt.io/changelog/" target="_blank">
                {useTranslation("Changelog")}
            </a>
            <a href="mailto:info@acpt.io" target="_blank">
                {useTranslation("Support")}
            </a>
            <a className="facebook" href="https://www.facebook.com/groups/880817719861018" target="_blank">
                {useTranslation("Facebook group")}
            </a>
        </nav>
    );
};

export default HeaderMenu;