import React from 'react';
import {Icon} from "@iconify/react";

const LanguagePlaceholder = () => {

    const language = document.globals.language;
    const availableLanguages = document.globals.available_languages;
    const languageLabel = availableLanguages.filter((l)=> l.value === language);

    if(languageLabel.length === 0){
        return null;
    }

    return (
        <div className="language-placeholder">
            <Icon icon="ion:language-outline" width="18px" color="#2271b1" />
            <span className="language-label">
                {languageLabel[0].label}
            </span>
        </div>
    );
};

export default LanguagePlaceholder;