import {wpAjaxRequest} from "../../utils/ajax";
import {fetchLicenseFailure, fetchLicenseInProgress, fetchLicenseSuccess} from "../actions/fetchLicenseActions";

export const fetchLicense = () => async (dispatch, getState) => {
    try {
        dispatch(fetchLicenseInProgress());
        const fetched = await wpAjaxRequest('fetchLicenseAction', {} );
        dispatch(fetchLicenseSuccess(fetched));
    } catch ( e ) {
        dispatch(fetchLicenseFailure(e));
    }
};
