import React, {useEffect, useRef, useState} from 'react';
import {changeCurrentAdminMenuLink, metaTitle, refreshPage} from "../../../utils/misc";
import Breadcrumbs from "../../reusable/Layout/Breadcrumbs";
import Modal from "../../reusable/Modal";
import {useDispatch, useSelector} from "react-redux";
import {fetchLicense} from "../../../redux/thunks/fetchLicense";
import Spinner from "../../reusable/Loader/Spinner";
import {wpAjaxRequest} from "../../../utils/ajax";
import {toast} from "react-toastify";
import {useHistory} from "react-router-dom";
import Layout from "../../reusable/Layout";
import ActionsBar from "../../reusable/Layout/ActionsBar";
import useTranslation from "../../../hooks/useTranslation";

const License = () => {

    // manage global state
    const dispatch = useDispatch();
    const {fetched:license, loading} = useSelector(state => state.fetchLicenseReducer);

    // manage local state
    const history = useHistory();
    const didMountRef = useRef(false);
    const [fetchedSuccess, setFetchedSuccess] = useState(null);
    const [modalVisible, setModalVisible] = useState(false);

    useEffect(() => {
        metaTitle(useTranslation(`Manage your license`));
        changeCurrentAdminMenuLink('#/license');
        dispatch(fetchLicense());
    }, []);

    // handle fetch outcome
    useEffect(() => {
        if (didMountRef.current){
            if(!loading){
                setFetchedSuccess(true);
            }
        } else {
            didMountRef.current = true;
        }
    }, [loading]);

    // deactivate the license
    const deactivateLicense = () => {
        wpAjaxRequest( useTranslation('deactivateLicenseAction'), {} )
            .then( res  => {
                refreshPage();
            })
            .finally(err => {
                toast.error(err);
            })
        ;
    };

    if(!fetchedSuccess){
        return <Spinner/>;
    }

    const button = <React.Fragment>

        <Modal title={`Confirm deactivating license`} visible={modalVisible}>
            <p>
                {useTranslation("Are you sure?")}
            </p>
            <p>
                <a
                    href="#"
                    className="acpt-btn acpt-btn-primary"
                    onClick={(e) => {
                        e.preventDefault();
                        deactivateLicense();
                    }}
                >
                    {useTranslation("Yes")}
                </a>
                &nbsp;
                <a
                    href="#"
                    className="acpt-btn acpt-btn-primary-o"
                    onClick={(e) => {
                        e.preventDefault();
                        setModalVisible(!modalVisible);
                    }}
                >
                    {useTranslation("No")}
                </a>
            </p>
        </Modal>
        <button
            onClick={(e) => {
                e.preventDefault();
                setModalVisible(!modalVisible);
            }}
            className="acpt-btn acpt-btn-danger"
        >
            {useTranslation("Deactivate")}
        </button>
    </React.Fragment>;

    return (
        <Layout>
            <ActionsBar
                title={useTranslation("Manage your license")}
                actions={button}
            />
            <main>
                <Breadcrumbs crumbs={[
                    {
                        label: useTranslation("Registered Custom Post Types"),
                        link: "/"
                    },
                    {
                        label: useTranslation("Your license")
                    }
                ]} />
                <div className="acpt-card">
                    <div className="acpt-card__header">
                        <div className="acpt-card__inner">
                            {useTranslation("Manage your registered license key")}
                        </div>
                    </div>
                    <div className="acpt-card__body">
                        <div className="acpt-card__inner">
                            <div className="acpt-form-group fixed-h">
                                <div className="acpt-form-label-wrapper">
                                    <label htmlFor="license">
                                        {useTranslation("Plugin version")}
                                    </label>
                                </div>
                                <div className="acpt-form-control-wrapper">
                                <span className={`plugin-version ${license.outOfDate ? 'old' : 'current'}`}>
                                    {license.currentVersion}
                                </span>
                                    {license.outOfDate && (
                                        <a href={license.upgradeUrl}>
                                            {useTranslation("Upgrade")}
                                        </a>
                                    )}
                                </div>
                            </div>
                            <div className="acpt-form-group fixed-h">
                                <div className="acpt-form-label-wrapper">
                                    <label htmlFor="license">
                                        {useTranslation("License")}
                                    </label>
                                </div>
                                <div className="acpt-form-control-wrapper">
                                    <code>
                                        {license.code}
                                    </code>
                                </div>
                            </div>
                            <div className="acpt-form-group fixed-h">
                                <div className="acpt-form-label-wrapper">
                                    <label>
                                        {useTranslation("Expiration date")}
                                    </label>
                                </div>
                                <div className="acpt-form-control-wrapper">
                                    {license.expiring_date ? license.expiring_date : useTranslation('Lifetime')}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </Layout>
    );
};

export default License;